import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/Index'
import Sala from '../components/Sala'

Vue.use(Router)

export default new Router({
    mode : 'hash',
    routes: [
        {
            path :'/',
            component : Index
        },
        {
            path :'/aluno',
            component : Index
        },
        {
            path: '/sala',
            component : Sala
        }



    ]
});
